@import "https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap";
* {
  margin: 0;
}

:root {
  --header-height: 60px;
  --main-text-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  padding: 0;
  display: inline-block;
}

.header__nav {
  display: inline-block;
}

.header__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.header__list-item:not(:first-child) {
  margin-left: 20px;
}

.header__link {
  line-height: var(--header-height);
  color: #000;
  text-align: center;
  text-transform: uppercase;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.header__link:hover, a.is-active {
  color: var(--main-text-color);
}

a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

